const API_BASE_URL = "http://localhost:8080/api";

export const END_POINT = {
  LOGIN: `${API_BASE_URL}/auth/login`,
  SIGNUP: `${API_BASE_URL}/auth/signup`,
  VERIFY_USER: `${API_BASE_URL}/auth/verifyUser`,
  VERIFY_TOKEN: `${API_BASE_URL}/auth/verifyToken`,
  PRODUCTS: `${API_BASE_URL}/products`,
  CATEGORIES: (id) => `${API_BASE_URL}/categories${id?'/'+id:''}`,
  GET_PRODUCT: (id) => `${API_BASE_URL}/products/item/${id}`,
  SEARCH: (route) => `${API_BASE_URL}/${route}/search`,
  FILTER: (filter) => `${API_BASE_URL}/filter/${filter}`,
  CONTACT: `${API_BASE_URL}/contact`,
  USERS: `${API_BASE_URL}/users`,
  GET_ROLE: `${API_BASE_URL}/auth/getRole`,
};
